/* Modal Overlay */
.movie-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(18, 18, 18, 0.95);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
  padding: 20px; /* safe padding on small screens */
  overflow-y: auto;
}

/* Container for modal content */
.modal-content {
  background-color: #000;
  color: #eee;
  padding: 30px 30px 40px;
  border-radius: 10px;
  max-width: 600px;
  width: 100%;
  text-align: center;
  position: relative;
  animation: fadeIn 0.3s ease-in-out;
  box-shadow: 0 0 20px rgba(255, 77, 77, 0.7);
}

/* Modal image */
.modal-content img {
  height: 300px;
  width: 100%;
  /* max-height: 400px; */
  object-fit: contain;
  border-radius: 8px;
  margin-bottom: 20px;
}

/* Close button */
.close-btn {
  position: absolute;
  top: 10px;
  right: 15px;
  font-size: 28px;
  color: #ff4d4d;
  background: none;
  border: none;
  cursor: pointer;
  transition: color 0.3s ease;
}

.close-btn:hover {
  color: #e60000;
}

/* Trailer button */
.trailer-btn {
  display: inline-block;
  margin-top: 15px;
  background-color: #ff4d4d;
  padding: 12px 24px;
  color: white;
  border-radius: 6px;
  font-weight: 700;
  text-decoration: none;
  transition: background 0.3s ease;
}

.trailer-btn:hover {
  background-color: #e60000;
}

/* Title style */
.best-from-2025 h2 {
  text-align: center;
  color: rgb(219, 68, 68);
  margin-bottom: 20px;
}

/* Parent container */
.best-from-2025 {
  display: flex;
  justify-content: center;
  width: 100%;
  flex-direction: column;
  align-items: center;
  padding: 0;
  margin: 0;
}

/* Fade-in animation */
@keyframes fadeIn {
  from {
    transform: scale(0.9);
    opacity: 0;
  }
  to {
    transform: scale(1);
    opacity: 1;
  }
}

/* Movies grid - fixed */
.movie-modal .movie-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(180px, 1fr)); /* responsive cards */
  gap: 20px;
  width: 100%;
  padding: 0;
  margin: 0 auto; /* keep centered */
  text-align: center;
}

/* Responsive tweaks */
@media (max-width: 640px) {
  .modal-content {
    padding: 20px;
  }

  .modal-content img {
    max-height: 250px;
  }

  .trailer-btn {
    padding: 10px 18px;
    font-size: 14px;
  }

  .movie-grid {
    grid-template-columns: repeat(auto-fill, minmax(140px, 1fr));
    gap: 15px;
  }
}
